import { fetchWithToken } from '@/utils/Keycloak'
//import config from '@/assets/config.json'

const backend = 'http://localhost:9999'

export function getMessages () {
  const url = `${backend}/messages`
  console.log('pouet2')
  return fetchWithToken(url, { method: 'GET' })
    .then((response) => {
        console.log('response', response)
        response.json()})
    .then(result => {
        console.log('mes messages : ', result)
        return result
    })
}

export function insertMessage (post) {
    const url = `${backend}/addmessage`
    return fetchWithToken(url, { method: 'POST',body: JSON.stringify({title:post.title, post:post.post})})
    .then((response) => {
        console.log('response', response)
        response.json()})
}

export function update (id) {
const params = new URLSearchParams()
params.append('id', id)
const url = `${backend}/post/${params.toString()}`
return fetchWithToken(url, { method: 'PUT' })
    .then(response => response.json())
    .then(result => {
        return result
    })
}

export function suppr (id) {
const params = new URLSearchParams()
params.append('id', id)
const url = `${backend}/post/${params.toString()}`
return fetchWithToken(url, { method: 'DELETE' })
    .then(response => response.json())
    .then(result => {
        return result
    })
}