import { fetchWithToken } from '@/utils/Keycloak'
//import config from '@/assets/config.json'

const backend = 'https://post.twilos.codes'

export function getPosts () {
  const url = `${backend}/status`
  console.log('pouet2')
  return fetch(url, { method: 'GET' })
    .then((response) => {
      console.log('pouet3')
        console.log('response', response)
        console.log('pouet4')
        response.json()})
    .then(result => {
        console.log('mes posts : ', result)
        return result
    })
}

export function insertPost (post) {
    const url = `${backend}/addpost`
    return fetchWithToken(url, { method: 'POST',body: JSON.stringify({title:post.title, post:post.post, pseudo:post.pseudo})})
    .then((response) => {
        console.log('response', response)
        response.json()})
}

export function update (id) {
const params = new URLSearchParams()
params.append('id', id)
const url = `${backend}/message/${params.toString()}`
return fetchWithToken(url, { method: 'PUT' })
    .then(response => response.json())
    .then(result => {
        return result
    })
}

export function suppr (id) {
const params = new URLSearchParams()
params.append('id', id)
const url = `${backend}/message/${params.toString()}`
return fetchWithToken(url, { method: 'DELETE' })
    .then(response => response.json())
    .then(result => {
        return result
    })
}