import * as Keycloak from 'keycloak-js';
import store from '@/store/store'
import Vue from 'vue'

/*export const keycloak = Keycloak({
    'url': 'http://localhost:8080/auth',
    'realm': 'twilos',
    'clientId': 'twilos'
})
*/
export const keycloak = Keycloak({
    'url': 'https://keycloak.twilos.codes/auth',
    'realm': 'master',
    'clientId': 'twilos'
})  

Vue.prototype.$keycloak = keycloak;
Vue.prototype.$store = store;

export const init = () => {
    return new Promise((resolve, reject) => {
        keycloak.init({ onLoad: 'login-required' })
        .then((auth) =>{
            if(!auth) {
                window.location.reload(); 
            } else {
                let payload = {
                    idToken: keycloak.idToken,
                    accessToken: keycloak.token,
                    pseudo:keycloak.tokenParsed.preferred_username,
                    nom:keycloak.tokenParsed.family_name,
                    prenom:keycloak.tokenParsed.given_name,
                  }
                  if (keycloak.token && keycloak.idToken && keycloak.token != '' && keycloak.idToken != '') {
                    store.commit("login", payload);
                  }
            }
            
            localStorage.setItem("vue-token", keycloak.token);
            localStorage.setItem("vue-refresh-token", keycloak.refreshToken);
   
            setInterval(() =>{
                keycloak.updateToken(70).success((refreshed)=>{
                  if (refreshed) {
                    Vue.$log.debug('Token refreshed'+ refreshed);
                  } else {
                    Vue.$log.warn('Token not refreshed, valid for '
                    + Math.round(keycloak.tokenParsed.exp + keycloak.timeSkew - new Date().getTime() / 1000) + ' seconds');
                  }
                }).catch(()=>{
                    Vue.$log.error('Failed to refresh token');
                });
            }, 60000)
            resolve()
        })
        .catch(() =>{
            
            document.href = "localhost:8080"
            Vue.$log.error("Authenticated Failed");
            reject()
        });
    })
}

export const fetchWithToken = (url, options) => {
    console.log('url', url)
    console.log('options', options)
    return new Promise((resolve, reject) => {
      const optionsWithHeaders = ((keycloak) => {
        console.log('keucloak', keycloak)
        const headers = Object.assign({}, options.headers, {
          'Authorization': 'Bearer ' + keycloak.token
        })
  
        return Object.assign({}, options, {
          headers: headers,
        })
      })
  
      return keycloak.updateToken(30).success(() => {
        return fetch(url, optionsWithHeaders(keycloak)).then(resolve)
      }).catch(function (err) {
        reject(err)
      })
    })
  }