<template>
  <!-- App.vue -->

    <!-- Probides the application the proper gutter -->
    <b-container fluid class="py-8 px-6">
      <b-row >
        <b-col cols="12 mt-5">
            <h2 class="title">Ecrire un tweet</h2>
            <b-card flat color="#EEEEEE" class="mt-3">
            <b-textarea v-model="post" color="teal" outlined label="Tweet">
              </b-textarea>
              </b-card>
            <div class="flex mt-3">
              <b-btn icon class="btn-primary mr-2"><img src="@/assets/addPicture.png" alt=""></b-btn>
              <b-btn class="btn-primary" @click="postPost">Poster</b-btn>
            </div>
        </b-col>
      </b-row>
    </b-container>

</template>

<style>
  .btn-primary{
    background-color: #17a2b8 !important;
  }
</style>
<script>

//import {mapState} from 'vuex'
import store from '@/store/store'
  export default {
   data: () => ({
    post:'',
    image:'',
  }),
  computed: {
     account () {
      return this.$store.state.keycloack
    },
  },
  created () {

  },
  methods: {
    postPost() {
      let post = {image:this.image, post:this.post, pseudo:this.account.pseudo}
      console.log(post)
      store.dispatch('insert',post)
    },
}
}
</script>